@import url(https://fonts.googleapis.com/css?family=Roboto:400,700&display=swap);
/* Roboto Font */

* {
  margin: 0;
  padding: 0;
  outline: 0;
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
}

body, input, button {
  font-family: Roboto, sans-serif;
}

main {
  width: 100%;
}
.container {
  max-width: 1132px;
  padding: 0px 16px;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 0 auto;
}

a {
  text-decoration: none;
  color: inherit;
}

h1 {
  font-size: 50px;
}
h2 {
  font-size: 38px;
}
header {
  display: flex;
  flex-direction: row !important;
  justify-content: space-between !important;
  align-items: center;
  height: 60px;
  padding: 0px !important;
  margin: 10px;
}

header a, header img {
  height: 100%; 
  width: auto;
  border-radius: 50%;
  padding: 2px;
}
header img {
  -webkit-filter: invert(1);
          filter: invert(1);
}

header ul {
  list-style: none;
  display: flex;
}
header ul li {
  padding: 10px;
}

@media(max-width: 600px) {
  header {
    justify-content: center;
    display: none;
  }
}
section#About {
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: center;
  min-height: calc(100vh - 60px);
}

section#About img {
  margin-top: -40px;
  width: 260px;
  height: auto;
  border-radius: 50%;
}

section#About svg {
  width: 25px;
  height: 25px;
  margin: 5px;
}

section#About h1 {
  margin-top: 16px;
  margin-bottom: 8px;
  font-size: 36px;
}
section#About p {
  color: #555;
  font-size: 16px;
  max-width: 360px;
  text-align: center;
}

section#About ul {
  list-style: none;
  display: flex;
  flex-direction: row;
}
.project-item {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  padding: 20px 0px;
}
.project-item.inverted {
  flex-direction: row-reverse;
}
.project-item img {
  display: flex;
  width: 46%;
  border-radius: 4px;
  box-shadow: 0px 2px 4px black;
}
.project-item div.details {
  display: flex;
  flex-direction: column;
  flex: 1 1;
  padding: 0px 40px;
}
.project-item div.details strong {
  font-size: 24px;
  margin-bottom: 24px;
}
.project-item div.details p {
  margin-bottom: 24px;
  color: #ccc;
}
.project-item div.details .links {
  display: flex;
  flex-direction: row;
}
.project-item div.details a {
  display: flex;
  justify-content: center;
  align-items: center;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  padding: 8px 6px;
}
.project-item div.details a span {
  margin-left: 4px;
}

@media(max-width: 900px) {
  .project-item {
    flex-direction: column !important;
    max-width: 600px;
  }
  .project-item img {
    width: 100%;
  }
  .project-item div.details {
    display: flex;
    flex-direction: column;
    flex: 1 1;
    padding: 20px 0px;
  }
}
section#Projects {
  padding: 20px;
  background-color: #212121;
  color: white;
}
section#Projects h2 {
  text-align: center;
  margin-bottom: 5px;
}
section#Projects h3.more-repositories {
  font-size: 24px;
  margin: 16px 0px;
}

section#Projects ul {
  list-style: none;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
}
ul li.card {
  display: flex;
  flex-direction: column;
  flex: 0 0 auto;
  border-radius: 5px;
  background-color: white;
  color: #212121;
  margin: 5px;
}
li.card a {
  padding: 20px;
}
li.card h3 {
  margin-bottom: 5px;
}
li.card footer {
  display: flex;
  flex: 1 1;
  flex-direction: row;
  margin-top: 10px;
}
li.card div.circle {
  background-color: grey;
  width: 18px;
  height: 18px;
  border-radius: 9px;
  margin-right: 5px;
}
section#Projects a.github-repositories {
  margin: 16px;
}

@media(max-width: 900px) {
  section#Projects ul {
    grid-template-columns: 1fr 1fr;
  }
}
@media(max-width: 600px) {
  section#Projects ul {
    grid-template-columns: 1fr;
  }
}
div#root > footer {
  background-color: #212121;
  color: white;
  text-align: center;
  padding: 8px;
}
